<template>
  <select
    v-model="selected"
    class="form-select mt-10 block w-full border p-3 rounded"
    @change="onChange"
  >
    <option value="0">Select Country</option>
    <option v-for="country in countries" v-bind:key="country.Country" v-bind:value="country.ID">
      {{ country.Country }}
    </option>
  </select>

  <button @click="clearCountry" v-if="stats.Country" class="bg-green-700 text-white rounded p-3 mt-10 focus:outline-none hover:bg-green-600">
            Clear Country
  </button>
</template>

<script>
export default {
    name:'CountrySelect',
    props: ['countries','stats'],
    data(){
      return{
        selected:0
      }
    },
    methods:{
      onChange(e){
       const country = this.countries.find((item) => item.ID === this.countries[e.target.options.selectedIndex].ID);
      
     
       this.$emit("get-country",country);
      },

      clearCountry()
      {
        this.$emit("clear-Country");
      }
    }
}
</script>